import React, { Component } from "react";
import { Link } from 'react-router-dom';
export default class Home extends Component{
  constructor(props){
    super(props);
    this.state = {
      sliderContent:[
        {color:"#2e6930",ico:"fa-television",title:"",url:"/test1",content:",,,. . , , . ,, , "},
        {color:"#05920A",ico:"fa-opencart",title:"Magento",url:"/test2",content:", Magento, , Magento, . Magento "},
        {color:"#0fcf16",ico:"fa-credit-card",title:"P2P",url:"/test3",content:",, , p2p, , P2PPCP2PP2P"},
        {color:"#9ecf0f",ico:"fa-dashboard",title:"",url:"/test4",content:", banner, "},
        {color:"#d6c80f",ico:"fa-mobile-phone",title:"",url:"/test5",content:", ,,. pc,"},
        {color:"#fdad73",ico:"fa-tablet",title:"",url:"/test6",content:",H5H5"},
        {color:"#edbd9a",ico:"fa-weixin",title:"",url:"/test6",content:""},
        {color:"#fed17d",ico:"fa-weixin",title:"",url:"/test6",content:""},
        {color:"#dfbf1a",ico:"fa-list-alt",title:"",url:"/test6",content:",,. "},
        {color:"#bee54c",ico:"fa-asl-interpreting",title:"banner",url:"/test6",content:"bannerbannerbanner banner"},
        {color:"#9ecf0f",ico:"fa-connectdevelop",title:"PHP",url:"/test6",content:"60%80%phpB2BB2CPHPPHPLINUXwindowsPHPWEBPHPOAAPP"}
      ],
      swiper:window.Swiper
    }
  }
  componentWillUnmount(){
    if (this.swiper) { 
      this.swiper.slideTo(0, 0);
      this.swiper.destroy();
      this.swiper = null;
     }
  }
  componentDidMount(){
    var _this = this, _item = _this.refs.header.classList, tempTimer;

    tempTimer = setTimeout(()=>{
      _item.add("active");
      clearTimeout(tempTimer);
    },200);

    _this.swiper = new _this.state.swiper('.swiper-container', {
        //loop:true, //
        autoplay:true,
        slidesPerView: 4,
        spaceBetween: 15,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      });
  }
  render(){
    var _this = this.state, tempCudeBox = [], tempCudeImg = [], tempSlider = [];
    for(var i = 0; i < 6; i++){
      tempCudeBox.push(
        <div key={i} className={`surface  key${i}`}>
          {this.props.cudeBox[i].content} 
        </div>
      );
      /*require*/
      tempCudeImg.push(
        <div key={i} className={`surface-in  index${i}`}>
          <img src={require(`../img/${i+2}.jpg`)} alt="" />
        </div>
      );
    }

    for(var j = 0, len2 = _this.sliderContent.length; j < len2; j++){
      tempSlider.push(
        <div key={j} className="swiper-slide">
          <div className="slideDetial" style={{backgroundColor:_this.sliderContent[j].color}}>
            <Link className="blockLink" to={_this.sliderContent[j].url }>
                <div className="ico">
                  <i className={`fa fa-5x  ${_this.sliderContent[j].ico} `}></i>
                </div>
                <h3 className="subTitle">{_this.sliderContent[j].title}</h3>
                <div className="content">
                  {_this.sliderContent[j].content}
                </div>
            </Link>
          </div>
        </div>
      );
    }

    return(
      <div className="home">
        <div className="headerTop" ref="header">
          <div className="box">
							<div className="cude">
                {tempCudeBox}
                {tempCudeImg}
							</div>
						</div>
        </div>
        <div className="homeMain main">
          <div className="swiper-container">
            <div className="swiper-wrapper">
                {tempSlider}
            </div>
            <div className="swiper-pagination"></div>
          </div>
        </div>
        <div id="footer"></div>
      </div>
    );

  }
}


